<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('business.topTitle')"></mHeader>
    <div class="caseTop">
      <caseTop :head-top="$t('business.caseTop')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
            <div slot="title">
              <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
            </div>
          <div class="tabList">
           <div class="case2">
             <ModuleTwo
               :twoP="$t('business.ci2twoP')"
               :twoSpan="$t('business.ci2twoSpan')"
               :twoSpan2="$t('business.ci2twoSpan2')"
             />
            </div>
            <div class="case3">
              <ModuleTen1
                :tenDd1="$t('business.ci2Ten1tenDd1')"
              />
            </div>
          </div>
        </van-tab>
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <div class="case10">
              <ModuleTen2
                caseInner2="true"

                :tenDt1="$t('business.ci2Ten2tenDt1')"
                :tenDd1="$t('business.ci2Ten2tenDd1')"
                :tenDt2="$t('business.ci2Ten2tenDt2')"
                :tenDd2="$t('business.ci2Ten2tenDd2')"
                :tenDt3="$t('business.ci2Ten2tenDt3')"
                :tenDd3="$t('business.ci2Ten2tenDd3')"
                :tenDt4="$t('business.ci2Ten2tenDt4')"
                :tenDd4="$t('business.ci2Ten2tenDd4')"
                :tenDt5="$t('business.ci2Ten2tenDt5')"
                :tenDd5="$t('business.ci2Ten2tenDd5')"
              />
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleTwo from "../../../components/module/ModuleTwo.vue"
  import ModuleTen2 from "../../../components/module/ModuleTen2.vue"
  import ModuleTen1 from "../../../components/module/ModuleTen1.vue"

  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        console.log(scrollTop);
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleTwo,
      ModuleTen2,
      ModuleTen1
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      .caseVantTab{
        height: 100%;
        .tabList{
          background: #fff;
          .case2{
           padding-top: 2rem;
         }
          .case3{
            padding-top: 1.2rem;
            padding-bottom: .8rem;
          }
        }
        .tabList2{
          background: #fff;
          .case10{
            padding-bottom: .8rem;
            padding-top: 1.82rem;
          }
        }
      }
    }
  }

</style>



